import tippy from 'tippy.js';
import 'tippy.js/dist/tippy.css';

export default {
  scrollToTop: false,
  head() {
    return {
      title: 'Liên hệ',
      meta: [
        { charset: 'utf-8' },
        { hid: 'description', name: 'description', content: 'Liên hệ với chúng tôi.' },
      ],
    };
  },
  computed: {
    social() {
      return this.$store.state.meta.social_media_links;
    },
  },
  mounted() {
    tippy('[data-tippy-content]');
  },
};
